export const BuyCoversPage: React.FC = () => {
    return (<CoversProvider>
        <ChilderWidget/>
    </CoversProvider>);
}

const ChilderWidget: React.FC = () => {

    return (
        <div style={{minHeight: "64vh"}}>
            <CoversHeader>
                <TabWidget/>
                <ButtonWidget to="/cover/my" text="Buy Cover" type="img"/>
            </CoversHeader>
            <ListWapStyle>
                <Switch>
                    <Route path={`/cover/all/${AliasEnum.Active}`} exact component={ActiveWidget}/>
                    <Route path={`/cover/all/${AliasEnum.Inactive}`} exact component={InActiveWidget}/>
                    <Route path={`/cover/all/${AliasEnum.Claims}`} exact component={ClaimsWidget}/>
                    <Route path={`/cover/all/${AliasEnum.Rewards}`} exact component={RewardsWidget}/>
                    <Redirect to={`/cover/all/${AliasEnum.Active}`}/>

                </Switch>

            </ListWapStyle>
        </div>
    )
}


const ActiveWidget: React.FC = () => {
    const history = useHistory();
    return (
        <>

            <SearchListWidget structure={(ev: any) => [
                {
                    inner: ev.name,
                    url: ev?.logo?.filename || '',
                    color: "#FFFFFF",
                    type: 'Imagelabel',
                    size: 28
                },
                {
                    inner: ev.period.toString(),
                    color: "#FFFFFF",
                },
                {
                    inner: getBalanceNumber(ev.cost, 18),
                    color: "#FFFFFF",
                },
                {
                    inner: `${DateUtils.dataString(ev.begin)} --- ${DateUtils.dataString(ev.end)}`,
                    color: "#FFFFFF",
                },
                {
                    inner: 'Claim',
                    color: "#3DB3F4",
                    textAlign: "center",
                    onClick: () => {
                        history.push('/cover/sub_mit_claim_page?data=' + JSON.stringify(ev),);
                    }

                }
            ]} isShowHeader={false} url={"/cover/user-list"} params={{'model': '0'}}>
                <div>Protocol</div>
                <div>Cover Amount</div>
                <div>Cover Cost</div>
                <div>Cover Period</div>
                <div>Action</div>
            </SearchListWidget>

        </>
    );
}


