import {useCallback, useContext, useEffect, useState} from "react";
import {useWeb3} from "../../../provider/web3_provider";
import {useWallet} from "use-wallet";

import {CardModel, ConvertCardInterface, defaultCardModel} from "../../../interface/start_mining";
import {PidMap} from "../../../server/capita_stake_server";
import {getBalanceNumber} from "../../../utils/formatBalance";




export const useCardFn = (itemModel: ConvertCardInterface) => {
    const {blockNumber, contrcts, web3} = useWeb3();
    const {account} = useWallet();

    const [model, setModel] = useState<CardModel>(defaultCardModel);
    const pid = PidMap[(itemModel.nick).toLowerCase()]

    const handelCard = useCallback(async () => {
        if (!itemModel.contract) return;
        let balance = await itemModel.contract.methods.balanceOf(account).call();
        let poolBalance = await itemModel.contract.methods.balanceOf(contrcts.capitaStakeAddress).call();

        let totalSupply = await itemModel.contract.methods.totalSupply().call();
         // let pendingNsure = await contrcts.capitaStakeContract.methods.pendingNsure(pid,account).call();
        let pendingNsure = 20 * 1e18;

        let ethBalance = await web3.eth.getBalance(itemModel.address);
        let trueBalance = ethBalance * poolBalance / totalSupply

        let userInfo = await contrcts.capitaStakeContract.methods.userInfo(pid, account).call();
        let apy = trueBalance > 0 ? (1.8 * 6400 * 365 * 0.001 / getBalanceNumber(trueBalance) * 1e2).toFixed(2) : 0;


        let dataModel: CardModel = {
            ...userInfo,
            balance,
            apy,
            poolBalance,
            ethBalance,
            totalSupply,
            trueBalance,
            pendingNsure,
        }
        setItemModel(itemModel, dataModel);
        setModel(dataModel);
    }, [setModel, model, account, pid],);
    useEffect(() => {
        handelCard();
    }, [blockNumber]);

    return model
}


function setItemModel(item: ConvertCardInterface, model: CardModel) {
    item.balance = model.balance;
    item.apy = model.apy;
    item.poolBalance = model.poolBalance;
    item.ethBalance = model.ethBalance;
    item.totalSupply = model.totalSupply;
    item.trueBalance = model.trueBalance;
    item.pendingNsure = model.pendingNsure;
}
